.vkuiEpic .Panel--game .vkuiPanel__in  {
  padding-bottom: 0;
}

.vkuiEpic .game.vkuiSnackbar {
  padding-bottom: var(--safe-area-inset-bottom);
}

.vkuiPanelHeader__content-in {
  font-weight: 700;
}
.icons--helth{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.container__word{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.letter__word {
width: 40px;
height: 40px;
color: var(--button_primary_background);
/* background-color: var(--button_secondary_background_highlighted); */
box-sizing: border-box;
margin: 3px;
border-radius: 4px;

border: solid 0.5px var(--header_tab_inactive_text);
/* --placeholder_icon_foreground_secondary */

display: flex;
font-size: 22px;
justify-content: center;
align-items: center;
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.icons .vkuiIconButton[disabled] { 
  opacity: 1;}
  
.Counter--icons{
  /* background: #aeb7c24a; */
  /* background: var(--background_highlighted);
  color: var(--button_primary_background); */
  position: absolute;
  top: -10px;
  right: -20px;

  min-width: 34px;
}

.letters {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 15px;
}
.letter {
  background-color: var(---background_content);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  line-height: 1;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  color: var(--button_primary_background);
  /* color: #132f63; */
  
}

/* Profile */
.profile--gradient{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
}

.profile--title_user_name{
  margin-bottom: 8px;
  margin-top: 20px; 
}
.profile--text_user_rank{
color: var(--text_secondary)
}



.iconLetter{
  position: absolute;
  bottom: 0;
  right: 0;

  background-color: #ffffff33;
  border-radius: 50%;
}


.vkuiEpic .vkuiFixedLayout--bottom.fixed--promo{
  padding-bottom: 0;
}
.vkuiSpacing.sepa {
  padding-left: 16px;
  padding-right: 16px;
}
.vkuiSpacing.sepa::after {
  content: "";
  display: block;
  height: 1px;
  background: currentColor;
  -webkit-transform-origin: center top;
  transform-origin: center top;
}

.keybord {
  box-shadow: 0px -7px 20px 2px rgb(0 0 0 / 6%), inset 0 0 5px 0 rgb(0 0 0 / 8%);
  /* box-shadow: 0px -7px 20px 20px rgb(0 0 0 / 6%), inset 0px 20px 20px 3px rgb(0 0 0 / 8%); */
  position: fixed;
  bottom: 0;
  padding-bottom: calc(var(--tabbar_height) + var(--safe-area-inset-bottom));
  left: 0;
  right: 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;

  display: flex;
    flex-direction: column;
    align-items: center;
}


.slider.vkuiSliderSwitch {
  width: 100px;
  height: 50px;
  border-radius: 25px;
}


.slider .vkuiSliderSwitch__slider {
  width: 50%;
  height: 50px;
  margin: 0;
  border-radius: 50%;
  box-sizing: border-box;
  box-shadow: 0px 5px 19px 1px rgb(0 0 0 / 18%);
  border: 0.3px solid var(--tabbar_active_icon);
}
.slider .vkuiSliderSwitch__slider:focus-visible { 
  outline: none;
}
.slider .vkuiSliderSwitch--secondActive {
  margin-left: -4px;
}